<template>
	<u-tabbar :list="list" active-color="#03958C" inactive-color="#A8A8A8" @change="topage"></u-tabbar>
</template>

<script>
	export default {
		data() {
			return {
				list: [{
						iconPath: "/static/homeIcon/deal.png",
						selectedIconPath: "/static/homeIcon/deal_select.png",
						pagePath: '/pages/salemanVisual/needDeal/index',
						text: '待办',
						customIcon: false
					},
					{
						iconPath: "/static/homeIcon/custom.png",
						selectedIconPath: "/static/homeIcon/custom_select.png",
						pagePath: '/pages/salemanVisual/customer/index',
						text: '客户',
						customIcon: false
					},
					{
						iconPath: "/static/homeIcon/house.png",
						selectedIconPath: "/static/homeIcon/house_select.png",
						pagePath: '/pages/salemanVisual/house/index',
						text: '房源',
						customIcon: false
					},
					{
						iconPath: "/static/homeIcon/more.png",
						selectedIconPath: "/static/homeIcon/more_select.png",
						pagePath: '/pages/salemanVisual/more/index',
						text: '更多',
						customIcon: false
					}
				],
			}
		},
		created(){
			// if(uni.getStorageSync('nowIdentity')==4 || uni.getStorageSync('nowIdentity')==5){
			// 	uni.hideTabBar()
			// }
		},
		mounted() {},
		methods: {
			topage(index) {
				switch (index) {
					case 0:
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/needDeal/index'
							})
						}, 200)
						break;
					case 1:
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/customer/index'
							})
						}, 200)
						break;
					case 2:
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/house/index'
							})
						}, 200)
						break;
					case 3:
						setTimeout(function() {
							uni.reLaunch({
								url: '/pages/salemanVisual/more/index'
							})
						}, 200)
						break
				}
			}
		},
		computed: {},
		watch: {}
	}
</script>

